*,
*::before,
*::after {
  box-sizing: border-box;
  transition: all .25s ease;
}

:root {
  --sidebar-min: 120px;
  --sidebar-max: 340px;
}

/* 1. ë°ìŠ¤í¬íƒ‘ ê¸°ë³¸ â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */
.main-content {
  display: flex;
  gap: 40px;
}

.sidebar {
  flex: 0 1 clamp(var(--sidebar-min), 24vw, var(--sidebar-max));
  text-align: center;
}

.sidebar .profile-img {
  width: 100%;
  max-width: var(--sidebar-max);
  aspect-ratio: 1/1;
  border-radius: 50%;
  object-fit: cover;
  height: auto;
  margin: 0 auto 20px;
}

.content-grid { flex: 1; }

/* Interests Â· Education */
.info-section {
  display: flex;                   /* ê¸°ë³¸ì€ flex ë‘ ì¹¸ */
  flex-wrap: wrap;
  gap: clamp(10px, 4vw, 52px);
  row-gap: 0px;
}

.info-section > :first-child {
  flex: 1 1 260px;
}

/* â· Educationâ€”ì˜¤ë¥¸ìª½ ì¹¸: ë” ë„“ê²Œ ìš°ì„  ë°°ë¶„ */
.info-section > :nth-child(2) {
  flex: 2 1 380px;
}


/* 2. iPad ì„¸ë¡œ (~1024â€¯px) â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */
@media (max-width: 1023px) {
  .main-content { gap: 32px; }
  .sidebar{
    flex:0 1 clamp(160px, 28vw, 260px);
  }
}

/* 3. ëª¨ë°”ì¼ (~866â€¯px) â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */
@media (max-width: 866px) {
  .main-content {
    flex-direction: column;
    align-items: center;
    gap: 32px;
    padding: 0 16px;
  }

  .sidebar{
    max-width: 350px;
    padding: 24px 0 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  /* â˜… ê·¸ë¦¬ë“œë¡œ ì „í™˜í•´ í•œ ì¤„ì— í•˜ë‚˜ì”© */
  .info-section {
    display: grid;
    grid-template-columns: 1fr;
    gap: clamp(16px, 5vw, 48px);
    row-gap: 0px;
  }
}

/* 4. ì´ˆì†Œí˜• í° (~400â€¯px) â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */
@media (max-width: 400px) {
  .sidebar {
    max-width: 200px;
    padding: 24px 0 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .profile-img {
    max-width: 200px;
    width: 100vw;
  }

  .sidebar-icons {
    flex-wrap: wrap;
    justify-content: center;
    gap: 14px;
    font-size: 1.5rem;
    margin-top: 14px;
  }
}